@import 'materialize-css/dist/css/materialize.min.css';

body {
  margin: 0;
  padding: 0;
  background-color: #282c34;
}

.App {
  width: 100%;
  text-align: center;
  position: relative;
}

.Overlay_show {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.68);
  transition: all 0.8s;
}
